@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
a {
  text-decoration: none;
  color: #000000;
}
.page-body {
  font-family: "PT Sans", "Arial", sans-serif;
  text-align: center;
  margin: 0 auto;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  background-color: #E7E5E5;
  color: #000000;
}
.page-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
  background-color: #FFFFFF;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1200px) {
  .desktop-hidden {
    display: none;
  }
}
.button {
  font-family: inherit;
  font-weight: bold;
  text-transform: inherit;
  border: none;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 21px;
  line-height: 26px;
  background-color: #81B3D2;
}
.button:hover {
  background-color: #669EC0;
}
.button:focus {
  background-color: #669EC0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496BD;
}
.button:disabled {
  color: #FFFFFF;
  background-color: #E5E5E5;
}
@media (min-width: 768px) {
  .button {
    padding: 16.5px 121px;
  }
}
.page-header {
  width: 100%;
  padding-bottom: 56px;
  z-index: 1;
}
@media (min-width: 768px) {
  .page-header {
    margin-bottom: 0;
    padding-bottom: 0;
    height: 56px;
  }
}
.main-nav__logo {
  position: absolute;
  max-width: 100%;
  height: 84px;
  left: 50%;
  transform: translate(-50%);
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__logo {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
  }
}
@media (min-width: 1200px) {
  .main-nav__logo {
    width: 187px;
    height: 97px;
  }
}
.main-nav-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav-list {
    position: absolute;
    width: 100%;
    max-width: 1200px;
    height: 56px;
    top: 0;
    display: flex;
  }
}
@media (min-width: 768px) {
  .main-nav-list li:nth-child(1) {
    padding-left: 14px;
  }
  .main-nav-list li:nth-child(2) {
    margin-right: 19%;
  }
  .main-nav-list li:nth-child(3) {
    padding-right: 17px;
  }
  .main-nav-list li:nth-child(4n+1) {
    margin-left: 35px;
  }
  .main-nav-list li:nth-child(4n+4) {
    margin-right: 26px;
  }
  .main-nav-list li:nth-child(4n+1),
  .main-nav-list li:nth-child(4n+2) {
    text-align: left;
  }
  .main-nav-list li:nth-child(4n+3),
  .main-nav-list li:nth-child(4n+4) {
    text-align: right;
  }
}
@media (min-width: 1200px) {
  .main-nav-list li:nth-child(2) {
    margin-right: 12%;
  }
  .main-nav-list li:nth-child(3) {
    padding-right: 17px;
  }
  .main-nav-list li:nth-child(4n+1) {
    margin-left: 76px;
  }
  .main-nav-list li:nth-child(4n+4) {
    margin-right: 70px;
  }
}
.main-nav__item {
  font-weight: bold;
  font-size: 18px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 20%;
    box-shadow: none;
    padding-top: 15px;
    font-size: 14px;
    line-height: 26px;
  }
}
.main-nav__item a {
  display: flex;
  min-height: 51.76px;
  outline: none;
  padding-top: 4px;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;
  background-color: #81B3D2;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.main-nav__item a:hover {
  color: #81B3D2;
  background-color: #669EC0;
}
.main-nav__item a:focus {
  color: #81B3D2;
  background-color: #669EC0;
}
.main-nav__item a:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496BD;
}
@media (min-width: 768px) {
  .main-nav__item a {
    display: inline;
    color: #000000;
    background-color: transparent;
    box-shadow: none;
  }
  .main-nav__item a:hover,
  .main-nav__item a:focus {
    color: #81B3D2;
    background-color: transparent;
  }
  .main-nav__item a:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }
}
.main-nav__item:last-child a {
  box-shadow: inset 0 -7px 7px -5px rgba(0, 0, 0, 0.2), 0 0 1px rgba(0, 0, 0, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-nav__item:last-child a {
    box-shadow: none;
  }
}
.main-nav__toggle {
  display: none;
  position: absolute;
  border: none;
  outline: none;
  cursor: pointer;
}
.footer {
  display: flex;
  flex-direction: column;
  padding: 67px 20px;
}
@media (min-width: 768px) {
  .footer {
    flex-direction: row;
    align-items: center;
