  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .footer__hashtag {
    padding: 39px 0 31px 0;
  }
}
.social-list {
  display: flex;
  margin: 0 auto;
  padding: 2px 0 0;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}
@media (min-width: 768px) {
  .social-list {
    padding: 6px 13px 0 0;
  }
}
.social-list__link {
  display: flex;
  width: 46px;
  height: 48px;
  margin: 0 3px;
  align-items: center;
  justify-content: center;
  background-color: #81B3D2;
}
.social-list__link:hover {
  background-color: #669EC0;
}
.social-list__link:focus {
  background-color: #669EC0;
}
